.form{
    display:flex;
    flex-direction:column;
    justify-content: center;
    margin: 1rem 2rem;

    @media all and (min-width: 768px) {
        flex-direction: row;
    }

       &-input{
           border: 1px solid grey;
           border-radius: 0.5rem;
           font-size: 1rem;
           padding:0.5rem 1rem;
           margin: 0.5rem;

           @media all and (min-width: 768px) {
            text-align: center;
        }
       }

       &-select{
           border: 1px solid grey;
           border-radius: 0.5rem;
           font-size: 1rem;
           margin: 0.5rem;
           text-align: center;
       }

       &-btn{
           border-radius: 0.5rem;
           font-size: 1rem;
           background-color: rgb(88, 197, 88);
           padding: 0.5rem 2rem;
           margin: 0.5rem;
       }

    }
